import { Component, Input, OnInit } from '@angular/core';
import { HttpService } from '../http.service';
import { Emplyoee } from './../employee';
@Component({
  selector: 'app-update-employee',
  templateUrl: './update-employee.component.html',
  styleUrls: ['./update-employee.component.css']
})
export class UpdateEmployeeComponent implements OnInit {

  @Input() parentdata=<Emplyoee>{};

  allCountry:any[]=[];
  issubmitDissabled : boolean = true;
  backendResponse : string = '';
  
  constructor(private service : HttpService) { }

  ngOnInit() {
    this.GetAllCountry();
  }

  GetAllCountry(){
    this.service.getAllCountry()
    .subscribe((response)=>{
      this.allCountry=(<any>response)
  });

  }

  onupdate(){
    this.parentdata.updatateddtm=Date.now();
    this.parentdata.updatedby=sessionStorage.getItem("username");
    console.log(this.parentdata);

    this.service.updateEmployee(this.parentdata)
    .subscribe((response)=>{
      this.backendResponse=response;
      this.issubmitDissabled=false;
    });

  }

}