import { HttpClient } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { Router } from '@angular/router';
import { HttpService } from '../http.service';

@Component({
  selector: 'app-addemployee',
  templateUrl: './addemployee.component.html',
  styleUrls: ['./addemployee.component.css']
})
export class AddemployeeComponent implements OnInit {
  
  allCountry:any[]=[];

  constructor(private service : HttpService,
    private route : Router) { }

  ngOnInit() {
    this.GetAllCountryFromBackend();

  }

  GetAllCountryFromBackend(){
    this.service.getAllCountry()
    .subscribe((response)=>{
      this.allCountry=(<any>response);
    })

}

onSubmit(f:NgForm){
  let obj={
    name:f.value.name,
    department:f.value.department,
    status:f.value.status,
    phoneno:f.value.phoneno,
    country:{
      cid:f.value.country.cid,
      cname:f.value.country.cname
    },
    createddtm:Date.now(),
    createdby:sessionStorage.getItem("username"),
    updatateddtm:Date.now(),
    updatatedby:sessionStorage.getItem("username")

  }

  this.service.addEmployee(obj)
  .subscribe((response)=>{
    console.log(response);
    this.route.navigate(["/home"]);
  })

}

}